@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Roboto', sans-serif;
  background-color: #705E78;
}

@media screen and (max-width: 768px) {
  * {
    background-color: #705E78;
  }
  
}
#inputs-radio-tasks {
  padding: 15px;
  background-color: aliceblue !important;  

}
.input-label {
  padding: 5px;
  font-size: 1.1rem;
  background-color: aliceblue !important;  

}
.task-input-text-container {
  display: flex;
  justify-content: center;
  background-color: aliceblue !important;
  padding: 30px;
}

.task-text-input {
  height: 35px;
  width: 230px;
  font-size: 1.1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
}

.task-save-button {
  padding: 8px;
  margin-left: 15px;
  font-size: 1.1rem;
}

#other-text-input {
  height: 25px;
  width: 200px;
  font-size: 1.1rem;
  padding: 5px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;  

}

#other-save-button {
  padding: 8px;
  margin-left: 15px;
  font-size: 1.1rem;
  background-color: #A1B5FF;
  border: none;
  border-radius: 5px;
}

.other-save {
  margin-top: 10px;
  padding: 5px;
  border-radius: 3px;
  margin-right: 10px;
  transition: 0.7s;
  font-size: 1.1rem;
  border: none;
  background-color: aliceblue !important;
}

.task-save-button {
  padding: 8px;
  margin-left: 15px;
  font-size: 1.1rem;
  background-color: #A1B5FF;
  border: none;
  border-radius: 5px;
}

.task-input {
  border: 1px solid #ccc;
  text-align: center;
  padding: 10px;
  border-radius: 8px;
  background-color: aliceblue !important;  
  margin-bottom: 20px;
  width: 30vw;
  margin: 0 auto;
}

