.task-list-big{
  width: 50vw;
  padding: 20px;
  margin: 0 auto;
}

.task-list-container {
  padding: 15px;
  font-size: 1.3rem;
  border: 1px solid #252526;
  border-radius: 3px;
  margin-bottom: 15px;
  box-shadow: 5px 10px #888888;
  background-color: aliceblue !important;
}
.done-task {
  text-decoration: line-through;
  background-color: #F2B5A7;
  transition: 0.7s;
  list-style-type: none;
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 5px;
  color: #252526;
}

.not-done-task {
  text-decoration: none;
  background-color: #92E887;
  transition: 0.7s;
  list-style-type: none;
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 5px;
  color: #252526;
}

.btn-task-list {
  padding: 5px;
  border-radius: 3px;
  margin-right: 10px;
  transition: 0.7s;
  font-size: 1.1rem;
  border: none;
  width: 80px;
}

.btn-task-list:hover {
  border: 2px solid #252526;
}

#false-btn {
  background-color: #A1B5FF;
  color: #252526;
  padding: 5px;
  border-radius: 3px;
  margin-right: 10px;
  transition: 0.7s;
  font-size: 1.1rem;
  border: none;
  width: 80px;
}

#true-btn {
  background-color: #FFD094;
  color: #252526;
  padding: 5px;
  border-radius: 3px;
  margin-right: 10px;
  transition: 0.7s;
  font-size: 1.1rem;
  border: none;
  width: 80px;
}

#edit-btn {
  background-color: #FFF69E;
  color: #252526;
}

#delete-btn {
  background-color: #EBAAA7;
  color: #252526;   
}

#extra-edit-input {
  height: 25px;
  width: 70%;
  font-size: 1.1rem;
  padding: 5px;
  background-color: #fff;
}

.extra-edit-container {
  padding: 20px !important;
  background-color: aliceblue !important;
}

#extra-edit-btn {
  margin-top: 10px;
  padding: 5px;
  border-radius: 3px;
  margin-right: 10px;
  transition: 0.7s;
  font-size: 1.1rem;
  border: none;
  width: 60px;
  background-color: #888888;
}

@media screen and (max-width: 768px) {
  .task-list-big {
    width: 100vw;
  }
  
  .task-list-container {
    width: 80%;
    padding: 15px;
    font-size: 1.3rem;
    border: 1px solid #252526;
    border-radius: 3px;
    margin-bottom: 15px;
    box-shadow: 5px 10px #888888;
    background-color: aliceblue !important;
  }
  .done-task {
    text-decoration: line-through;
    background-color: #F2B5A7;
    transition: 0.7s;
    width: 90%;
    list-style-type: none;
    margin-bottom: 15px;
    padding: 10px;
    border-radius: 5px;
    color: #252526;
  }
  
  .not-done-task {
    text-decoration: none;
    background-color: #92E887;
    transition: 0.7s;
    width: 90%;
