    margin-bottom: 15px;
    padding: 10px;
    border-radius: 5px;
    color: #252526;
  }

  .btn-task-list {
    padding: 5px;
    border-radius: 3px;
    margin-right: 10px;
    transition: 0.7s;
    font-size: 1.1rem;
    border: none;
    width: 80px;
  }

  .btn-task-list:hover {
    border: 2px solid #252526;
  }

  #false-btn {
    background-color: #A1B5FF;
    color: #252526;   
  }

  #true-btn {
    background-color: #FFD094;
    color: #252526;
  }

  #edit-btn {
    background-color: #FFF69E;
    color: #252526;
  }

  #delete-btn {
    background-color: #EBAAA7;
    color: #252526;   
  }

  #extra-edit-input {
    height: 25px;
    width: 200px;
    font-size: 1.1rem;
    padding: 5px;
    background-color: #fff;
  }

  .extra-edit-container {
    padding: 20px !important;
    background-color: aliceblue !important;
  }

  #extra-edit-btn {
    margin-top: 10px;
    padding: 5px;
    border-radius: 3px;
    margin-right: 10px;
    transition: 0.7s;
    font-size: 1.1rem;
    border: none;
    width: 60px;
    background-color: #888888;
  }
}

#filter-label {
